<template>
  <div class="priority-modal">
    <div class="bar-text" @click="open"></div>
  </div>
</template>
<script>
import { getLogs } from "@/api/tools";
export default {
  methods: {
    open() {
      this.$confirm(
        "",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      )
        .then(() => {






          this.$message({
            type: "success",
            message: "!",
          });
          console.log("");
          getLogs().then((res) => {
          window.open(res.data.filepath)
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
};
</script>
<style lang="scss">
.el-message-box__wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 30%;
}
</style>


